<template>
  <section class="container mt-5">
    <section class="text-center mb-3">
      <h6 class="py-2">
        本次消費總共
        <span class="text-price h5">${{totalPrice}}</span>
      </h6>
      <h6 class="py-2">總計{{totalNum}}件商品</h6>
    </section>
    <section class="mb-5">
      <section class="border mx-auto mb-3">
        <div class="py-4" v-for="(item,index) in cartData" :key="index">
          <div class="flex-center mx-auto w-75 position-relative">
            <img :src="item.imgUrl" height="70px">
            <div>
              <a>
                <h5>{{item.fullId}} {{item.title}}-{{item.gender}}</h5>
              </a>
              <h5>規格: {{item.colorObject.name}} - {{item.sizeObject.name}}</h5>
            </div>
            <div class="px-3 ml-auto flex-center">{{item.qty}}件</div>
            <div class="flex-center ml-4">$ {{item.qty*item.price}}</div>
          </div>
        </div>
      </section>
      <section class="price-section d-flex mb-5">
        <table class="ml-auto w-50 text-right p-2">
          <td>
            <tr>共{{totalNum}}件</tr>
            <tr></tr>
            <tr></tr>
            <tr></tr>
          </td>
          <td>
            <tr>商品金額</tr>
            <tr>運費小計</tr>
            <tr>帳戶扣抵</tr>
            <tr>總計</tr>
          </td>
          <td>
            <tr>${{totalPrice}}</tr>
            <tr>尚未選擇</tr>
            <tr>$0</tr>
            <tr>${{totalPrice}}</tr>
          </td>
        </table>
      </section>
    </section>
    <section class="border mb-5 mx-auto">
      <div class="border-bottom px-5 py-3 h4 mb-0">取貨人資訊</div>
      <div class="px-5">
        <div class="py-3 border-bottom">
          <div class="w-20 d-inline-block h4">姓名</div>
          <input
            type="text"
            class="w-80 d-inline-block border-0"
            v-model="receiverData.name"
            placeholder="請輸入姓名"
          >
          <div v-if="alertShow.name">
            <div class="w-20 h-0 d-inline-block"></div>
            <div class="w-80 d-inline-block text-price">此欄為必填</div>
          </div>
        </div>
        <div class="py-3 border-bottom">
          <div class="w-20 d-inline-block h4">聯絡電話</div>
          <input
            type="text"
            class="w-80 d-inline-block border-0"
            v-model="receiverData.phone"
            placeholder="請輸入電話號碼"
          >
          <div v-if="alertShow.phone">
            <div class="w-20 d-inline-block"></div>
            <div class="w-80 d-inline-block text-price">此欄為必填</div>
          </div>
        </div>
        <div class="py-3">
          <div class="w-20 d-inline-block h4">送貨地址</div>
          <input
            type="text"
            class="w-80 d-inline-block border-0"
            v-model="receiverData.address"
            placeholder="請輸入地址"
          >
          <div v-if="alertShow.address">
            <div class="w-20 d-inline-block"></div>
            <div class="w-80 d-inline-block text-price">此欄為必填</div>
          </div>
        </div>
      </div>
    </section>
    <section class="border mb-5 mx-auto">
      <div class="border-bottom px-5 py-3 h4 mb-0">發票類型</div>
      <div class="d-flex px-5 py-3">
        <a class="w-50 pr-2" @click="receiverData.invoice = '1',receiverData.invoiceData='請輸入統一編號'">
          <div
            class="invoice-item h5 py-3 rounded text-center"
            :class="{'invoice_select': receiverData.invoice==='1'}"
          >個人-二聯電子發票</div>
        </a>
        <a class="w-50 px-2" @click="receiverData.invoice = '2',receiverData.invoiceData=''">
          <div
            class="invoice-item h5 py-3 rounded text-center"
            :class="{'invoice_select': receiverData.invoice ==='2'}"
          >公司統一編號</div>
        </a>
      </div>
      <div class="text-unclick py-3 px-5 h5" v-if="receiverData.invoice === '1'">
        依財政部規定，發票已託管，無需開立紙本發票。	
      </div>
      <div class="py-3 px-5" v-if="receiverData.invoice === '2'">
        <div class="d-inline-block w-20">統一編號</div>
        <input type="text" class="d-inline-block w-80 border-0 text-unclick" placeholder="請輸入統一編號" v-model="receiverData.invoiceData">
        <div class="w-100 text-price" v-if="alertShow.invoiceData">此欄為必填</div>
      </div>
    </section>
    <section class="d-flex">
      <router-link to="/check" class="bg-primary rounded text-center py-3 text-white w-20">重選付款方式</router-link>
      <a
        @click="routeToCheck3"
        class="bg-primary ml-auto rounded text-center py-3 text-white w-20"
      >送出</a>
    </section>
  </section>
</template>
<script>
export default {
  name:'check2',
  data() {
    return {
      receiverData: {
        name: '',
        phone: '',
        address: '',
        invoice: '',
        invoiceData: '',
      },
      alertShow:{
        name: false,
        phone: false,
        address: false,
        invoice: false,
        invoiceData: false,
      },
      allCheck: false
    };
  },
  computed: {
    totalPrice() {
      return this.$store.state.totalPrice;
    },
    totalNum() {
      let tempNum = 0;
      this.cartData.forEach(element => {
        tempNum += element.qty;
      });
      return tempNum;
    }
  },
  methods: {
    routeToCheck3() {
      for(let item in this.receiverData){
        if(this.receiverData[item] === ''){
          this.alertShow[item] = true
        }else{
          this.alertShow[item] = false
        }
      }
      for(let item in this.alertShow){
        if(this.alertShow[item]){
          return
        }
      }
      this.allCheck = true
      if(this.allCheck){
        this.$store.commit('SETRECEIVE', this.receiverData)
        this.$router.push('/check/3')
      } 
    }
  },
  created() {
    this.cartData = this.$store.state.cart;
    this.$store.commit("CHECKSTEP", 2);
  }
};
</script>
<style lang="scss" scoped>
.price-section {
  background-color: #fbfbfb;
}
a {
  text-decoration: none;
}
.invoice-item {
  border: gray 1px solid;
}
.invoice_select {
  color: #593f34;
  border: #593f34 2px solid;
}

</style>
